<template>
  <div class="article-preview">
      <ul>
        <li v-on:click="onTagClick(tag)" v-for="(tag, tagIndex) in article.tags" :key="tagIndex">
          <tag-pill :text="tag" />
        </li>
      </ul>
      <h3>
        <router-link :to="path" v-html="title"></router-link>
      </h3>
      <h4 v-html="subtitle"></h4>

      <p>{{ bodyPreview }}</p>
  </div>
</template>

<script>
import removeMD from 'remove-markdown'
import TagPill from '@/components/TagPill.vue'

export default {
  props: ['article', 'index'],

  components: {
    TagPill
  },

  methods: {
    // when a tag is clicked tell the parent component
    onTagClick (tag) {
      this.$emit('tag-click', tag)
    }
  },

  computed: {
    // generates the URL for the article based off the slug set in the data
    path () {
      if (this.article.slug) {
        return `/article/${this.article.slug}`
      }
      return `/article/${this.index}`
    },

    bodyPreview () {
      const { body } = this.article
      // remove all markdown
      const noMarkdown = removeMD(body)

      // truncate the result
      const trimmed = noMarkdown.length > 100 ? noMarkdown.substr(0, 100) + '...' : noMarkdown

      return trimmed
    },

    title () {
      const matches = this.article.matches.filter(x => x.prop === 'title')

      // if there were no matches, just return it as is
      if (!matches.length) {
        return this.article.title
      }

      // storing this to modify as we go
      let title = this.article.title

      // as we start to add in higlights, the length of the string will increase, so this is to compensate for that
      let indexOffset = 0

      // sort the matches in terms of their index order (so we start highlighting from start to end)
      const orderedMatches = matches.sort((a, b) => a.index - b.index)

      orderedMatches.forEach(match => {
        // add in our offset
        const index = match.index + indexOffset

        // get the text we've matched
        const realText = title.substr(index, match.length)

        // wrap it in a span with our highlight class
        const replacement = `<span class="highlight">${realText}</span>`

        // create our new title
        title = title.substr(0, index) + replacement + title.substr(index + match.length)

        // tally up our offset
        indexOffset += replacement.length - realText.length
      })

      return title
    },

    // basically the same as title code but highlighting the subtitle instead
    subtitle () {
      const matches = this.article.matches.filter(x => x.prop === 'subtitle')

      if (!matches.length) {
        return this.article.subtitle
      }

      let subtitle = this.article.subtitle
      let indexOffset = 0

      const orderedMatches = matches.sort((a, b) => a.index - b.index)

      orderedMatches.forEach(match => {
        const index = match.index + indexOffset
        const realText = subtitle.substr(index, match.length)
        const replacement = `<span class="highlight">${realText}</span>`

        subtitle = subtitle.substr(0, index) + replacement + subtitle.substr(index + match.length)

        indexOffset += replacement.length - realText.length
      })

      return subtitle
    }
  }
}
</script>

